body {
    background-color: #dfe3ea; /* 배경색 설정 */
    font-family: Arial, sans-serif; /* 기본 폰트 설정 */
  }
  
  .container {
    text-align: center; /* 가운데 정렬 */
    padding: 20px;
  }
  
  .header {
    background-color: white;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
    width: 80%;
    max-width: 600px;
  }
  
  .crack-container {
    background-color: #626262;
    padding: 20px;
    border-radius: 10px;
    width: 80%;
    max-width: 600px;
  }
  
  .crack-card {
    background-color: white;
    padding: 20px;
    margin: 10px auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 80%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .crack-card p {
    margin: 0 0 10px 0;
  }
  
  .crack-card img {
    max-width: 100%;
    height: auto;
    aspect-ratio: 2 / 1;
  }
  